

import doctorOrOrg from '../../components/DoctorOrOrg';
import moduleTitle from '../../components/ModuleTitle';
import tabBar from '../../components/TabBar'
import searchBar from '../../components/SearchBar';
import tools from '../../lib/tools.js';
import Toast from 'vux/src/components/toast/index.vue'

import BScroll from 'better-scroll';

export default {
name:'orgList',
components:{doctorOrOrg,moduleTitle,Toast,tabBar,searchBar},
data(){
return {
id:'',
showToast:false,
toastMsg:'',
tempService:[],
pageNumber:1,
pageSum:0,
container:null,
dragging: false,
times: 0,
loading: false,
loadable:false,
pullUpObj:{
type:0,
text:''
},// 0  1 2
isShowPicker:false,
searchValue:'',
provinceArr:[], // 
cityArr:[],     // 
townArr:[],     // 
checkedText:'',
checkedProvince:'',
checkedProvinceText:'',
checkedCity:'',
checkedCityText:'',
checkedTown:'',
checkedTownText:'',
pickerValue:[],
}
},
mounted(){


















this.init();
this.initProvince();
},
beforeDestory() {













},
methods:{
init(queryObj){
var vm = this;
this.id = (localStorage.getItem('md') || tools.getRequestParam('managerId') || tools.getCookie('user_id'));//md===userid
if(!this.id){return}
let data = {pageSize:10,pageNumber:this.pageNumber,roleId:2,userStatus:1,shieldJY:1};
if(queryObj){
console.log('- - ',queryObj)
for(let key in queryObj){
data[key] = queryObj[key];
}
}
console.log('-',data)
this.$http.post('/backstage/query',data).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data.list && data.data.list.length==0){
vm.showToast = true;
vm.toastMsg = '';
if(vm.pullUpObj.type == 0){
vm.tempService = [];
}
}else{
var tempArr = [];
for(let i=0;i<data.data.list.length;i++){
let item = data.data.list[i];
tempArr.push({src:item.imageUrl,title:item.name,desc:item.skills,url:'/m/orgIndex.html?md='+item.managerId})
}
this.pageSum = data.data.pageSum;
this.loading = false;
this.loadable = (this.pageSum>1);
if(this.pullUpObj.type==0){
vm.tempService = tempArr;
vm.pullUpObj.type = 1;
}else{
vm.tempService = [...vm.tempService,...tempArr];
}
}
this.checkedProvince = '';
this.checkedCity = '';
this.checkedTown = '';
}else{
this.loading = false;
this.loadable = false;
console.log(data.resultCode);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
this.isShowPicker = false;
},(error) => {
this.loading = false;
this.loadable = false;
console.log(error);
vm.showToast = true;
vm.toastMsg = '';
})
},
touchDown(e) {

this.pageY = e.changedTouches ? e.changedTouches[0].pageY : e.pageY;

},
touchMove(e){
const container = this.container;
const pageY = e.changedTouches ? e.changedTouches[0].pageY : e.pageY;
const moveY = pageY - this.pageY;

if (moveY < 0 && (container.scrollTop + Math.min(global.innerHeight,container.clientHeight)) >= container.scrollHeight) {
e.preventDefault()
this.dragging = true;
if (moveY < -80 && this.dragging) {
this.pageY = pageY;
if(this.pageSum==this.times){
this.started = false;
this.loading = false;
this.loadable = false;
this.dragging = false;
}else{
this.times += 1;
this.started = true;
this.loading = true;
this.loadable = true;
this.pageNumber += 1;
this.init();
}
}
}
},
touchUp(e){
this.dragging = false
},
nextPage(){
if(!(this.pageSum==this.times)){
this.times += 1;
this.pageNumber += 1;
console.log('nextPage');
this.init({province:this.checkedProvince,city:this.checkedCity,town:this.checkedTown,name:this.searchValue});
}else{
this.pullUpObj.type = 2;
this.pullUpObj.text = ''
}
},
reLoad(){
this.pullUpObj = {
type:0,
text:''
};
this.times = 0;
this.pageNumber = 1;
this.init({province:this.checkedProvince,city:this.checkedCity,town:this.checkedTown,name:this.searchValue});
},
showPickerClick(){
this.isShowPicker = !this.isShowPicker;
},
search(e){
let vm = this;
let theEvent = e || window.event;
console.log(theEvent);
this.searchValue += '';



this.pullUpObj = {
type:0,
text:''
};
this.pageNumber = 1;
this.pageSum = 0;
this.times = 0;
theEvent.preventDefault();
theEvent.target.blur();
this.init({province:this.checkedProvince!=''?this.checkedProvince:'',city:this.checkedCity,town:this.checkedTown,name:this.searchValue});
return false;
},
initProvince(){
let vm = this;
this.$http.get('/address/getAllProvince',{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
this.provinceArr = data.data;
this.cityArr = [];
this.townArr = [];
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
getCityByProvinceCode(item){
console.log(item);
if(item.name==''){
this.isShowPicker = false;
this.pullUpObj = {
type:0,
text:''
};
this.pageNumber = 1;
this.pageSum = 0;
this.times = 0;
this.checkedProvince == '';
this.checkedCity == '';
this.checkedTown == '';
this.init({province:'',city:'',town:'',name:this.searchValue});
return false;
}
this.checkedProvince = item.code;
this.checkedProvinceText = item.code;
this.checkedText = item.name;
let vm = this;
this.$http.get('/address/getCityByProvinceCode?provinceCode='+item.code,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
if(data.data && data.data.length==0){
this.init({province:this.checkedProvince!=''?this.checkedProvince:'',city:this.checkedCity,town:this.checkedTown,name:this.searchValue});
return ;
}else{
this.cityArr = data.data;
this.townArr = [];
}
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
getTownByTownCode(item){
console.log(item);
this.checkedCity = item.code;
this.checkedCityText = item.code;
this.checkedText = item.name;
let vm = this;
this.$http.get('/address/getTownByTownCode?cityCode='+item.code,{}).then((result) => {
let data = result.data;
if(data.successful && (data.status==200)){
this.townArr = data.data;
}else{
console.error(data);
vm.showToast = true;
vm.toastMsg = data.resultCode.message;
}
},(error) => {
console.error(error);
vm.showToast = true;
vm.toastMsg = '';
});
},
clickPickerTown(item){
console.log(item);
this.checkedTown = item.code;
this.checkedTownText = item.code;
this.checkedText = item.name;
this.init({province:this.checkedProvince,city:this.checkedCity,town:this.checkedTown,name:this.searchValue});
}
}
}



