const FastClick = require('fastclick')
FastClick.attach(document.body);
import BScroll from 'better-scroll';
export default {
    name:'scrollBox',
    props: {
        /**
         * 1 scroll
         * 2 scroll
         * 3 scrollswipescroll
         */
        probeType: {
            type: Number,
            default: 1
        },
        /**
         * click
         */
        click: {
            type: Boolean,
            default: true
        },
        /**
         * 
         */
        scrollX: {
            type: Boolean,
            default: false
        },
        /**
         * 
         */
        listenScroll: {
            type: Boolean,
            default: false
        },
        /**
         * 
         */
        data: {
            type: Array,
            default: null
        },
        /**
         * 
         */
        pullup: {
            type: Boolean,
            default: false
        },
        /**
         * 
         */
        pulldown: {
            type: Boolean,
            default: false
        },
        /**
         * 
         */
        beforeScroll: {
            type: Boolean,
            default: false
        },
        /**
         * scroll
         */
        refreshDelay: {
            type: Number,
            default: 20
        },
        /**
         * loadingloading
         * isShow: false
         * showIcon: false,    // loadingicon
         * status: ''  // '...', '', '', ''
         */
        loadingStatus: {
            type: Object,
            default: function () {
                return {
                    showIcon: false,
                    status: ''
                };
            }
        },
        /**
         * 
         */
        pulldownUI: {
            type: Boolean,
            default: false
        },
        /**
         * 
         */
        pullupUI: {
            type: Boolean,
            default: false
        },
        pullUpObj:{
            type:Object,
            default:{type:0,text:''}
        }
    },
    data(){
        return {
            loadingConnecting: false,
            pulldownTip: {
                text: '',     // 
                rotate: ''    // icon-rotate
            },
            pullUpTip: {
                text: '',     //   
                rotate: ''    // icon-rotate
            },
        }
    },
    mounted() {
        // DOMbetter-scroll
        setTimeout(() => {
            this._initScroll()
        }, 20)
    },
    methods: {
        _initScroll() {
            if (!this.$refs.wrapper) {
                return
            }
            // better-scroll
            this.scroll = new BScroll(this.$refs.wrapper, {
                probeType: this.probeType,
                click: this.click,
                scrollX: this.scrollX,
                bounce:true,
            });

            // 
            if (this.beforeScroll) {
                this.scroll.on('beforeScrollStart', () => {
                    this.$emit('beforeScroll')
                })
            }

            // 
            if (this.listenScroll) {
                //  || this.pulldown || this.pullup

                // this.scroll.on('scroll', (pos) => {
                //     this.$emit('scroll', pos)
                // })
                let me = this;
                this.scroll.on('scroll', (pos) => {
                    if (this.listenScroll) {
                        me.$emit('scroll', pos);
                    }
                })
            }

            // 
            if (this.pulldown) {
                this.scroll.on('touchEnd', (pos) => {
                    // 
                    if (pos.y > 80) {
                        setTimeout(() => {
                            // 
                            this.pulldownTip = {
                                text: '',     // 
                                rotate: ''    // icon-rotate
                            }
                        },300);
                        this.$emit('pulldown');
                    }
                });
            }

            // 
            if (this.pullup) {
                this.scroll.on('scrollEnd', (pos) => {
                    // 
                    if (this.scroll.y <= (this.scroll.maxScrollY + 100)) {
                        setTimeout(() => {
                            // 
                            this.pullUpTip = {
                                text: (this.pullUpObj.type==2?this.pullUpObj.text:this.pullUpText),     // 
                                rotate: ''    // icon-rotate
                            }
                        },300);
                        this.$emit('scrollToEnd');
                        // this.$emit('pulldown');
                    }
                });
            }
        },
        disable() {
            // better-scrolldisable
            this.scroll && this.scroll.disable()
        },
        enable() {
            // better-scrollenable
            this.scroll && this.scroll.enable()
        },
        refresh() {
            // better-scrollrefresh
            this.scroll && this.scroll.refresh()
        },
        scrollTo() {
            // better-scrollscrollTo
            this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
        },
        scrollToElement() {
            // better-scrollscrollToElement
            this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
        }
    },
    watch: {
        // refreshDelayrefresh
        data(n,o) {
            setTimeout(() => {
                this.refresh()
            }, this.refreshDelay)
        }
    }
}



